import { css } from '@emotion/react';

export const baseStyles = css`

  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-display: swap;
  }

  ::-webkit-input-placeholder {
    /* Edge */
    color: #f3f3f3;
  }

  :-ms-input-placeholder {
    /* Internet Explorer */
    color: #f3f3f3;
  }

  ::placeholder {
    color: #f3f3f3;
  }

  html,
  body {
    font-family: -apple-system, BlinkMacSystemFont, 'Apple SD Gothic Neo', 'SF Pro Text', 'SF Pro Display', 'Segoe UI', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif,
      'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
    font-size: 16px;
    scroll-behavior: smooth;
  }

  a {
    transition: color 0.15s;
    // text-decoration: none;
  }

  a:hover {
    // text-decoration: none;
  }

  body {
    font-family: 'Apple SD Gothic Neo' 'SF Pro Text', 'SF Pro Display';
  }

  .visibleMobileOnly {
    display: none;
  }

  .displayInline {
    display: inline-block;
  }

  pre {
    border: 0 !important;
    background-color: #fafafa; 
  }


  /* 
    
    GNB section 
    
  */

  .GNBWrapper {

  }
  
  .removePadd {
    padding: 0 !important;
  }

  .GNBDefault {
    background-color: #131313;
    border-radius: 0;
    // border-top: 0;
    margin-bottom: 0;
    border: 0;
    box-shadow: -1px 0px 4px 1px rgba(175, 158, 232, 0.4);
    -webkit-box-shadow: -1px 0px 4px 1px rgba(175, 158, 232, 0.4);
    -moz-box-shadow: -1px 0px 4px 1px rgba(175, 158, 232, 0.8);
    -o-box-shadow: -1px 0px 4px 1px rgba(175, 158, 232, 0.4);
    z-index: 1;
    padding: 0 24px;
    position: relative;
    height: 72px;
  }

  .GNBHeader {
    min-width: 335px;
    flex: 1 1;
  }

  .GNBDefault,.GNBHeader {
    display: flex;
    align-items: center;
  }

  .GNBNav {
    display: flex;
    align-items: center;
    padding: 0 60px;
  }

  .GNBUL {
    -webkit-overflow-scrolling: touch;
  }

  .GNBUL:hover li a {
    color: #919191 !important;
  }

  .GNBULRight {
    /* position: absolute;
    right: 30px; */
  }

  .GNBUL li {
    list-style-type: none;
  }

  .GNBUL li a {
    color: #fff !important;
    font-family: 'SF Pro Text';
    font-weight: 700;
    font-size: 18px;
    line-height: 100%;
    margin: 0 16px;
    letter-spacing: -0.022em;

  }

  .GNBUL li a.active {
    border-bottom: 2px solid #fff;
  }

  .GNBUL:hover li a.active {
    border-bottom: 2px solid #919191;
  }

  .GNBUL li a:hover {
    color: #fff !important;
  }

  .GNBUL li a.active:hover {
    border-bottom: 2px solid #fff;
  }
  
  .GNBheaderTitle {
    font-family: 'SF Pro Display';
    height: auto;
    font-size: 22px;
    line-height: 22px;
    font-weight: 900;
    color: #fff !important;
    text-transform: uppercase;
  }

  .GNBheaderTitle a {
    color: #fff;
  }

  .GNBheaderTitle a:hover {
    text-decoration: none;
    opacity: 0.8;
  }

  .GNBheaderTitleTablet {
    display: inline-block;
    font-family: 'SF Pro Display';
    height: auto;
    font-size: 22px;
    line-height: 22px;
    font-weight: 900;
    color: #fff !important;
    text-transform: uppercase;
  }

  .GNBheaderTitleMobile {
    display: inline-block;
    font-family: 'SF Pro Display';
    height: auto;
    font-size: 22px;
    line-height: 22px;
    font-weight: 900;
    color: #fff !important;
    text-transform: uppercase;
    position: absolute;
    left: 20px;
  }

  .GNBheaderTitleTabletSVG {
    display: inline-block;
  }

  .GNBMobileWrapper {
    display: none;
  }

  .GNBTabletNav {
    display: none;
    min-width: 135px;
    width: 100%;
  }

  .mobileLNB {
    -webkit-transition: top 0.5s, bottom 0.5s;
  }

  .mobileMenuButton {
    width: 48px;
    height: 48px;
    position: absolute;
    right: 8px;
    padding: 16px 0px;
    display: none;
    border-radius: 24px;
    background-color: #1f1f1f;
  }

  .mobileMenuButton .iconBar {
    display: block;
    width: 20px;
    height: 1.5px;
    border-radius: 1px;
    margin: 5px auto 0;
    background-color: #e2e2e2;
  }

  .mobileMenuButton .iconBar:first-child {
    margin-top: 0px;
  }

  /* 
    
    LNB section 
    
  */

  .LNBWrapper {
  }

  .LNBUL li {
    list-style-type: none;
    width: auto;
    list-style: none;
  }

  .LNBUL li a {
    color: #636363;
    font-family: 'SF Pro Text';
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    letter-spacing: -0.022em;
    text-decoration: none;
    display: flex;
    align-items: center;
    padding: 11px 24px 11px 24px;
    border-style: solid none solid solid;
    border-width: 1px 0px 1px 1px;
    border-color: transparent currentcolor transparent transparent;
  }

  .LNBUL li a:hover {
    background-color: #fafafa;
    color: #131313 !important;
  }

  .LNBUL li .active a {
    font-weight: 700;
    background-color: #f1f1f1;
    color: #131313 !important;
  }

  .LNBUL .item .item {
    margin-left: 16px;
  }

  .lnbPart {
    font-family: 'SF Pro Text';
    font-weight: 400;
    font-size: 18px;
    color: #131313;
    line-height: 130%;
    letter-spacing: -0.022em;
    padding: 24px 24px 22px 24px;
  
  }

  // .lnbNav {
  //   color: #131313 !important;
  // }

  // .lnbNav .active > a {
  //   background-color: #ededed;
  //   color: #131313 !important;
  // }

  .firstLevel ul .item ul .item {
    border-left: 1px solid #e6ecf1;
  }

  .firstLevel > ul > .item {
    margin-left: 0 !important;
  }

  /* 
    
    Markdown section 
  
  */

  .titleWrapper {
    display: flex;
    align-items: center;
    padding-bottom: 40px;
    // border-bottom: 1px solid rgb(230, 236, 241);
    margin-bottom: 32px;
  }

  .addPaddTopBottom {
    padding: 96px 0;
  }

  .preRightWrapper {
    display: block;
    margin: 0px;
    flex: 1 1 0%;
    padding: 16px;
    text-align: right;
  }

  .nextPreviousTitle {
    display: block;
    margin: 0px;
    padding: 0px;
    transition: color 200ms ease 0s;
  }

  .nextPreviousTitle span {
    font-family: 'SF Pro Display';
    font-weight: 700;
    font-size: 23px;
    line-height: 1.3;
    color: #131313;
  }

  .smallContent {
    display: block;
    margin: 0px;
    padding: 0px;
  }

  .smallContent span {
    font-family: 'SF Pro Text';
    font-weight: 400;
    font-size: 14px;
    line-height: 1.3;
    color: #919191;
  }

  /* **************************** */

  .nextRightWrapper {
    display: block;
    margin: 0px;
    padding: 16px;
    flex: 1 1 0%;
  }

  /* tables.css */
  table {
    padding: 0;
  }

  table tr {
    border-top: 1px solid #cccccc;
    margin: 0;
    padding: 0;
  }

  table tr:nth-child(2n) {
    background-color: #f8f8f8;
  }

  table tr th {
    font-weight: bold;
    border: 1px solid #cccccc;
    text-align: left;
    margin: 0;
    padding: 6px 13px;
  }

  table tr td {
    border: 1px solid #cccccc;
    text-align: left;
    margin: 0;
    padding: 6px 13px;
  }

  table tr th :first-child,
  table tr td :first-child {
    margin-top: 0;
  }

  table tr th :last-child,
  table tr td :last-child {
    margin-bottom: 0;
  }
  /* end - tables.css */

  /* Image styling */
  img {
    max-width: 100%;
  }
  /* end image */
  .githubBtn {
    display: flex;
    align-items: center;
    font-size: 16px;
    padding: 10px 0px;
    padding-left: 15px;
    max-height: 40px;
  }
  .githubBtn span span {
    display: flex;
    align-items: center;
  }

  .communitySection {
    font-size: 24px;
    font-weight: 700;
  }
  .authorSection {
    padding: 20px 0;
  }
  .authorSection,
  .authorName {
    display: flex;
    align-items: center;
  }
  .authorImg img {
    width: 75px;
    height: 75px;
    border-radius: 50%;
    min-width: 75px;
    max-width: 75px;
    min-height: 75px;
    max-height: 75px;
  }
  .authorDetails {
    padding-left: 10px;
  }
  .authorDesc {
    padding-top: 5px;
    font-size: 14px;
  }
  .authorName img {
    margin-left: 10px;
    display: inline-block;
    width: 20px;
  }
  .authorName img:hover {
    opacity: 0.7;
  }

  /*   */ 

  .heading1 {
    font-size: 32px;
    font-family: 'SF Pro Display', 'Apple SD Gothic Neo';
    font-weight: 700;
    line-height: 1.3;
    margin-bottom: 16px;
    margin-top: 32px;
    color: #131313;
  }

  .heading2 {
    font-size: 26px;
    font-family: 'SF Pro Display', 'Apple SD Gothic Neo';
    font-weight: 700;
    line-height: 1.3;
    margin-bottom: 16px;
    margin-top: 32px;
    color: #131313;
  }

  .heading3 {
    font-size: 20px;
    font-family: 'SF Pro Display', 'Apple SD Gothic Neo';
    font-weight: 700;
    line-height: 1.3;
    margin-bottom: 16px;
    margin-top: 32px;
  }

  .heading4 {
    font-size: 16px;
    font-family: 'SF Pro Display', 'Apple SD Gothic Neo';
    font-weight: 700;
    line-height: 1.3;
    margin-bottom: 16px;
    margin-top: 32px;
  }

  .heading5 {
    font-size: 14px;
    font-family: 'SF Pro Display', 'Apple SD Gothic Neo';
    font-weight: 700;
    line-height: 1.3;
    margin-bottom: 16px;
    margin-top: 32px;
  }

  .heading6 {
    font-size: 12px;
    font-family: 'SF Pro Display', 'Apple SD Gothic Neo';
    font-weight: 700;
    line-height: 1.3;
    margin-bottom: 16px;
    margin-top: 32px;
  }

  .paragraph {
    margin: 16px 0px 32px;
    line-height: 1.625;
  }

  .pre {
    font-size: 14px;
    margin: 0px;
    padding: 16px;
    overflow: auto;
  }

  /* 
    
    Search section 
    
  */

  .formElement {
    background-color: transparent;
    padding: 4px;
    border-radius: 5px;
    // position: relative;
  }

  .formElement:focus {
    outline: none;
    border: none;
  }

  .formElement svg path {
    fill: #2fd2c5;
  }

  .searchInput {
    width: calc(100% - 26px);
    background-color: transparent!important;
    border-width: 0!important;
    color: #c2c2c2
  }

  .searchInput:focus,.searchInput:focus-within,.searchInput:hover,.searchInput:visited {
    outline: none;
    border: 0
  }

  .searchWrapper {
    padding-left: 0;
    padding-right: 20px;
    flex: 1 1;
    max-width: 600px;
    position: relative
  }

  .searchWrapper a {
    font-weight: 500
  }

  .poweredBy {
    font-size: 0.6em;
    text-align: end;
    padding: 0;
  }

  .hitWrapper {
    background-color: #fff;
    padding: 0.7em 1em 0.4em;
    border-radius: 4px;
    position: absolute;
    width: 80vw;
    max-width: 30em;
    top: 40px;
    border: 1px solid #ccc;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.16);
    height: auto;
    max-height: 80vh;
    overflow: scroll;
    left: 0;
  }

  .hitWrapper ul li {
    margin-top: 0.7em;
    padding-top: 0.7em;
    border-top: 1px solid;
    list-style-type: none;
  }

  .hitWrapper ul li:first-child {
    border-top: 0px;
    margin-top: 0px;
    color: black !important;
    padding: 0px;
  }

  .showResults {
    display: block;
  }

  .hideResults {
    display: none;
  }

  .hitWrapper span {
    color: black;
    font-size: 14px;
  }

  /* 
    
    Footer section 
  
  */

  .footerContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #f2f2f3;
    padding: 30px 60px;
  }

  .footerLinks ul {
    font-family: 'SF Pro Text';
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #555;
    list-style: none;
    padding: 0;
    margin: 0 0 20px 0;

    @media (min-width: 768px) {
      display: flex;
    }
  }

  .footerLinks li {
    margin-top: 5px;
    margin-bottom: 5px;
    float: left;
    margin-right: 5px;

    @media (min-width: 768px) {
      margin-left: 6px;
      margin-right: 6px;
    }
  }

  .copyright {
    font-family: 'SF Pro Text';
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #8e8e8e
  }

  /* 
    
    Mobile section 

  */
 
  @media (max-width: 767px) {
    .hiddenMobile {
      display: none !important;
    }

    .responsive {
      // margin-top: 15px;
      position: relative;
      // padding-bottom: 20px;
      border-top: 1px solid #fff;
    }

    .headerTitle {
      padding-right: 50px;
      font-size: 16px;
    }

    .GNBDefault {
      height: 64px;
      padding: 0;
    }

    .GNBMobileWrapper {
      display: flex;
      min-width: auto;
      align-items: center;
    }

    .mobileLNB {
      display: block;
    }

    .mobileLNB .GNBUL {
      display: none;
    }

    .mobileLNB.responsive .visibleMobileOnly {
      display: block;
    }

    .mobileLNB.responsive .GNBUL {
      display: block;
      text-align: left;
    }

    .heading1 {
      font-size: 26px;
    }

    .heading2 {
      font-size: 20px;
    }

    hr {
      margin-top: 0;
      margin-bottom: 0;
    }

    .separator {
      margin-top: 20px;
      margin-bottom: 20px;
    }
    .GNBULRight {
      position: static;
    }
    .GNBUL {
      // display: flex;
      // align-items: center;
      // margin: 7.5px 0px;
    }
    .GNBUL li {
      height: 37px;
    }
    .GNBUL li a {
      font-size: 14px;
      padding: 10px 15px;
    }

    .mobileMenuButton {
      margin-right: 0;
      display: block;
      // position: absolute;
      // left: 11px;
      // top: 15px;
      // background: #fff;
    }

    .GNBHeader {
      display: flex;
      min-width: auto;
      padding-right: 0;
      align-items: center;
    }

    .titleWrapper {
      padding: 0 15px;
      display: block;
    }

    .mobileView {
      text-align: left !important;
      padding-left: 0 !important;
    }

    // .formElement svg path {
      //   fill: #001934;
      // }
  
      // .visibleMobileView {
      //   display: block !important;
      // }
  
      // .searchInput {
      //   color: #001934;
      // }
  
      // .socialWrapper {
      //   position: absolute;
      //   right: 10px;
      //   top: 29px;
      // }

    .searchWrapper {
      // padding: 0px 0;
      // padding-top: 0px;
      // position: absolute;
      // bottom: 0px;
      // width: calc(100% - 70px);
      // position: absolute;
      // left: 40px;
      // top: 8px;
    }
    .hitWrapper {
      width: 100%;
      right: 0;
      top: 35px;
      max-height: fit-content;
      position: static;
    }
  }

  
  /* 
    
    Tablet section 
  
  */

  @media (min-width: 768px) and (max-width: 1023px) {

    .hiddenTablet {
      display: none !important;
    }

    .GNBTabletNav {
      display: block;
      text-align: center; 
    }
  }
`;
